#about-me-component {
  display: flex;
  flex-direction: column;

  flex-grow: 1;
  height: 100%;

 
  width:100%;

  p {
    margin: 0.5em;
  }

  .about-me-content {
    overflow: auto;
    .interest-chip,
    .skill-chip,
    .language-chip,
    .academic-title-chip {
      margin: 0.4em;
      background-color: #1591f035;
      color: #333;
      border-radius: 16px;
   
      font-size: 14px;
      font-weight: bolder;
    }

    .social-links {
      font-weight: bolder;

      display: flex;
      flex-grow: 1;

      a {
        text-decoration: none;

        color: black;
      }

      .social-icon-link {
        font-weight: bolder;
        margin: 0.2em;
      }
    }
  }
}
