#projects-component {
    display: flex;
    flex-direction: column;

    flex-grow: 1;
    height: 100%;

    #projects-summary {
        overflow: auto;
        padding: 0cm 1cm;
    }

    #navigation-to-projects-wrapper {
        display: flex;

        flex-wrap: wrap;

        flex-grow: 1;
        justify-content: center;
        align-items: flex-end;

        align-content: end;

        margin: 1em;

        button {
            flex-grow: 1;
            height: min-content;
            margin: 0.3em;
            align-self: flex-end;
        }
    }

    #projects-wrapper {
        flex-grow: 1;
        height: 100%;
        overflow: auto;
    }
}
