#content-component {
    display: flex;
    flex-direction: column;
    align-items: center;

    height: 100%;
    //background-color: aquamarine;

    h2 {
        height: min-content;
        width: 100%;
        text-align: center;
    }

    #contnet-with-cards {
        overflow: auto;

        display: flex;
        align-items: start;
        justify-content: center;
        align-content: flex-start;
        flex-direction: row;

        flex-wrap: wrap;

        //background-color: aqua;

        .card-item-component {
            display: flex;
            flex-direction: column;

            // background-color: rgba(0, 0, 255, 0.309);

            background-color: rgba(215, 255, 255, 0.42);

            border-radius: 10px;

            margin: 0.5em;
            padding: 0.2em;

            @media screen and (min-width: 30cm) {
                margin: 2em;
                padding: 0.5em;
            }

            max-width: 20cm;

            width: 100%;

            a {
                text-decoration: none;
                color: black;
            }

            .card-item-main-content {
                // background-color: cadetblue;

                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: center;

                .card-item-description-wrapper {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;

                    width: 100%;

                    flex-wrap: wrap;

                    article {
                        display: flex;

                        align-items: center;
                    }

                    .chips {
                        display: flex;
                        justify-content: center;
                        flex-wrap: wrap;

                        margin: 0.5em;

                        .one-chip {
                            margin: 0.1em;
                        }
                    }
                }

                .project-cover-image-wrapper {
                    width: 100%;

                    // background-color: aquamarine;

                    .project-cover-image {
                        border-radius: 5px;

                        width: 100%;
                        height: 100%;

                        // background-color: aquamarine;
                    }
                }
            }
        }
    }
}
