.jobs-component {
    display: flex;
    flex-direction: column;
    height: 100%;

    .jobs-list {
        overflow: auto;

        .one-job-card {
            background-color: #1591f035;


            margin:0.5em;
            padding: 0.5em;

    
            border-radius: 16px;

            .position-chip, .skill-chip {

                margin:0.1em;
   
                
                background-color: rgba(195, 228, 255, 0.6);
                color: #333;
                border-radius: 16px;
                
                font-size: 14px;
                font-weight: bolder;
            }
        }
    }
}
